.DropdownMenu {
    position: relative;
    width: 100%;
    max-height: 300px;
    display: flex;
    flex-direction: column;
    transition: all 0.4s ease-out;
}
  

.DropdownMenu[open=""] .DropdownMenu__button {
    border-radius: 5px;
}
  
.DropdownMenu__button {
    transition: all 0.4s;
    background-color: #eee;
    border: none;
    border-radius: 5px;
    padding: 12px 15px;
    width: 100%;
    cursor: pointer;
}
  

  
.DropdownMenu__options {
    padding-left: 0;
    padding-top: 0;
    margin-top: 0;
    list-style: none;
    overflow: auto;
    transition: all 0.4s ease-out;
    background-color: #eee;
    border-bottom-left-radius: 5px;
    border-bottom-right-radius: 5px;
}

.DropdownMenu__option {
    cursor: pointer;
    padding: 10px;
    font-size: 12px;
}
  
.DropdownMenu__option:hover {
    background-color: #012326;
    color: white;
}
  